<template>
  <div class="login">
    <div class="form">
      <h1>
        <center>后 台 登 录</center>
      </h1>
      <br/>
      <form @submit="submitHandler">
        用户名：
        <input v-model="user.username" placeholder="输入账号"/>
        密码：
        <input type="password" v-model="user.password" placeholder="输入密码"/>
        <button class="btn-submit">登 录</button>
      </form>
      <br/>
    </div>
  </div>
</template>

<script>
  import API from '../model/apis/user.requests'

  export default {
    name: "Login",
    data() {
      return {
        user: {
          username: 'jmx',
          password: 'DDJ&EDJ1314forever'
        }
      }
    },
    methods: {
      submitHandler(e) {
        e.preventDefault()
        let hint = ''
        if (this.user.username === '')
          hint = '请输入账号'
        else if (this.user.password === '')
          hint = '请输入密码'
        if (hint !== '') {
          this.$message(hint)
          return
        }
        API.login(this.user).then(data => {
          if (data) {
            localStorage.setItem('ttl_id', data.id)
          }
          this.$router.push('/jmx/home')
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  .login {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  .form {
    border-radius: 5px;
    width: 400px;
    background: #fff;
    /*margin-top: 60px;*/
    margin-left: auto;
    margin-right: auto;
    padding: 20px 40px;
    input {
      border-radius: 5px;
      border: 1px solid #dfdfdf;
      display: block;
      height: 40px;
      width: 368px;
      margin: 5px auto 20px auto;
      padding: 0 15px;
    }
    .btn-submit {
      background: #0086b3;
      color: #ffffff;
      width: 400px;
      height: 60px;
      border-radius: 5px;
      margin-top: 20px;
      font-size: 16px;
    }
  }
</style>



